export const DINGPAN_SELECT = Symbol('DINGPAN_SELECT');

export const DINGPAN_REMOVE = Symbol('DINGPAN_REMOVE');

export const DINGPAN_UPDATE = Symbol('DINGPAN_UPDATE');

export function selectDingpanFile() {
  return {
    type: DINGPAN_SELECT,
  };
}

export function removeDingpanFile(node) {
  return {
    type: DINGPAN_REMOVE,
    payload: {
      node,
    },
  };
}

export function updateDingpanFile(refer, data) {
  return {
    type: DINGPAN_UPDATE,
    payload: {
      node: refer,
      data,
    },
  };
}
